
import axios from 'axios'
import jsonp from 'axios-jsonp'
import { mapState, mapActions } from 'vuex'
import { defineComponent } from 'vue'

async function getSymbols(market, page = 0, page_size = 13) {
  const { data } = await axios({
    url: 'http://m.ytcj.com:8080/action/market/get_symbols',
    adapter: jsonp,
    params: { market, page, page_size }
  })

  return data
}

export default defineComponent({
  props: {
    market: Object
  },
  computed: {
    ...mapState('quotation', ['quotations', 'fields'])
  },
  methods: {
    ...mapActions('quotation', ['registerSymbols']),
    getCellClass({ row, column }) {
      const v = row[column.className]
      return v < 0 ? 'green' : v > 0 ? 'red' : '';
    }
  },
  async mounted() {
    this.$watch(() => this.market && this.market.id, async market => {
      if (market) {
        const { symbols } = await getSymbols(market)
        this.registerSymbols({ symbols: symbols.map(s => s.id) })
      }
    })
  }
})
