<template>
  <div id="chart-container" />
</template>

<script>
import axios from 'axios'
import jsonp from 'axios-jsonp'
import { defineComponent, watch, ref } from 'vue'
import Highstock from 'highcharts/highstock'

import indicators from 'highcharts/indicators/indicators-all'
indicators(Highstock)

function restoreData(arr, digits) {
  for (let i = 1; i < arr.length; i++) {
    arr[i] += arr[i - 1]
  }

  const factor = 10 ** digits
  for (let i = 0; i < arr.length; i++) {
    arr[i] /= factor
  }
}

export default defineComponent({
  props: {
    name: String,
    symbol: String
  },
  setup(props) {
    let chart = null
    const zq = ref('day')

    watch(() => props.symbol, async symbol => {
      const { data: [, [digits, ts, open, high, low, close], [,,[,,,vol]] ] } = await axios({
        url: 'http://www.baring.cn:81/chartdata',
        adapter: jsonp,
        params: { callback: '', symbol, zb: 'VOL', zq: zq.value, n: 200 }
      })

      restoreData(ts, -3)
      restoreData(open, digits)
      restoreData(vol, 0)
      if (high) restoreData(high, digits)
      if (low) restoreData(low, digits)
      if (close) restoreData(close, digits)

      const series = [
        open && high && low && close ? {
            type: 'candlestick',
            id: 'ohlc',
            name: symbol,
            data: ts.map((t, idx) => [t, open[idx], high[idx], low[idx], close[idx]])
        } : {
          type: 'line',
          id: 'ohlc',
          name: symbol,
          data: ts.map((t, idx) => [t, open[idx]])
        }, {
          type: 'column',
          id: 'volume',
          name: 'Volume',
          data: ts.map((t, idx) => [t, vol[idx]]),
          yAxis: 1
        }, {
          type: 'pc',
          id: 'overlay',
          linkedTo: 'ohlc',
          yAxis: 0
        }, {
          type: 'macd',
          id: 'oscillator',
          linkedTo: 'ohlc',
          yAxis: 2
        }
      ]

      if (chart) chart.destroy()
      chart = Highstock.stockChart('chart-container', {
        chart: {
          animation: false,
          // zoomType: 'x',
          panning: true,
          panKey: 'shift'
        },
        title: {
          text: props.name
        },
        // subtitle: {
        //   text: 'All indicators'
        // },
        legend: {
          enabled: false
        },
        rangeSelector: {
          enabled: false
        },
        yAxis: [{
          height: '60%'
        }, {
          top: '60%',
          height: '20%'
        }, {
          top: '80%',
          height: '20%'
        }],
        tooltip: {
          animation: false,
          enabled: true,
          // followTouchMove: false,
          split: true
        },
        plotOptions: {
          series: {
            animation: false,
            dataGrouping: { enabled: false }
            // showInLegend: true,
          }
        },
        series
      })
    })

    return { zq }
  }
})
</script>

<style scoped>
#chart-container {
  width: 100;
  height: 100%;
}
</style>
