<template>
  <el-table
    :data="quotations.filter(q => q.price)"
    :cell-class-name="getCellClass"
    @row-click="row => $emit('selected', row)"
  >
    <el-table-column
      prop="name"
      label="名称"
    />

    <el-table-column
      prop="updown"
      label=""
      class-name="updown"
      width="16"
      :formatter="(row, col, v) => v > 0 ? '↑' : v < 0 ? '↓' : ''"
    />

    <el-table-column
      prop="price"
      label="现价"
      class-name="fluctuation"
      :formatter="(row, col, v) => v && v.toFixed(row.digits)"
    />

    <!-- <el-table-column
      prop="fluctuation"
      label="涨跌"
      class-name="fluctuation"
      :formatter="(row, col, v) => v && v.toFixed(row.digits)"
    /> -->

    <el-table-column
      prop="fluctuationRate"
      label="涨跌幅"
      class-name="fluctuation"
      :formatter="(row, col, v) => v && (v * 100).toFixed(2) + '%'"
    />
  </el-table>
</template>

<script lang="ts">
import axios from 'axios'
import jsonp from 'axios-jsonp'
import { mapState, mapActions } from 'vuex'
import { defineComponent } from 'vue'

async function getSymbols(market, page = 0, page_size = 13) {
  const { data } = await axios({
    url: 'http://m.ytcj.com:8080/action/market/get_symbols',
    adapter: jsonp,
    params: { market, page, page_size }
  })

  return data
}

export default defineComponent({
  props: {
    market: Object
  },
  computed: {
    ...mapState('quotation', ['quotations', 'fields'])
  },
  methods: {
    ...mapActions('quotation', ['registerSymbols']),
    getCellClass({ row, column }) {
      const v = row[column.className]
      return v < 0 ? 'green' : v > 0 ? 'red' : '';
    }
  },
  async mounted() {
    this.$watch(() => this.market && this.market.id, async market => {
      if (market) {
        const { symbols } = await getSymbols(market)
        this.registerSymbols({ symbols: symbols.map(s => s.id) })
      }
    })
  }
})
</script>


<style scoped>
:deep(.red) {
  color: red;
}

:deep(.green) {
  color: green!important;;
}
</style>