<template>
  <el-container>
    <el-header>
      <el-menu
        mode="horizontal"
        :default-active="$route.matched.length > 0 && $route.matched[0].path"
        :router=true
      >
        <el-submenu index="/" :disabled="!currentMarket">
          <template #title>{{currentMarket && currentMarket.name || '数据读取中'}}</template>
          <div v-for="group in markets" :key="group.group">
            <el-menu-item :disabled=true>{{group.group}}</el-menu-item>
            <el-menu-item
              v-for="market in group.markets"
              :key="market.id"
              index="/"
              @click="setCurrentMarket(market)"
            >
              {{market.name}}
            </el-menu-item>
          </div>
        </el-submenu>
        <el-menu-item index="/about">关于</el-menu-item>
      </el-menu>
    </el-header>
    <el-main>
      <router-view />
    </el-main>
  </el-container>

</template>

<script lang="ts">
import { mapState, mapMutations } from 'vuex'
import { defineComponent } from 'vue'

export default defineComponent({
  computed: { ...mapState(['markets', 'currentMarket']) },
  methods: { ...mapMutations(['setCurrentMarket']) }
})
</script>

