import { createStore } from 'vuex'
import _ from 'lodash'
import axios from 'axios'
import jsonp from 'axios-jsonp'

import quotation from './quotation'

export default createStore({
  state: {
    markets: [],
    currentMarket: null
  },
  mutations: {
    setMarkets(state, markets) {
      state.markets = markets
      if (!state.currentMarket) {
        state.currentMarket = markets[0].markets[0];
      }
    },
    setCurrentMarket(state, currentMarket) {
      state.currentMarket = currentMarket
    }
  },
  actions: {
    async refreshMarkets({ commit }) {
      const { data } = await axios({
        url: 'http://m.ytcj.com:8080/action/market/list',
        adapter: jsonp
      })

      const groups = _.chain(data)
        .groupBy(m => m.group)
        .map((markets, group) => ({ group, markets }))
        .value()

      commit('setMarkets', groups)
    }
  },
  modules: {
    quotation
  }
})
